.homescreen{
    min-height: 100vh;
    background-image: url(/assets/images/stadium_overlay.png), url(/assets/images/stadium.jpg);
    background-size: cover;

    display: flex;
}
.homescreen__body{
    width: 100%;
    position: relative;
}
.homescreen h1{
    margin: 0;
}
.homescreen__title{
    text-align: right;
    color: white;
    font-size: 32px;
    margin-bottom: 4rem;
    line-height: 1.4;
}
@media(max-width: 992px){
    .homescreen__title{
        font-size: 19px;
    }
}
.homescreen__content{
    position: absolute;
    bottom: 4rem;
    right: 4rem;
    max-width: 46%;
}
@media(max-width: 992px){
    .homescreen__content{
        bottom: 1rem;
        right: 1rem;
        font-size: 17px;
        max-width: 100%;
    }
}
.homescreen__logos{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
.homescreen__logos svg:first-child{
    margin-right: 5rem;
}
@media(max-width: 992px){
    .homescreen__logos svg:first-child{
        margin-right: 0.7rem;
        margin-left: 0.7rem;
        width: 50%;
    }
    .homescreen__logos svg:nth-child(2){
        width: 30%;
    }
}