.secondscreen{

}

.secondscreen__body{
    margin-top: 4rem;
    display: flex;
}
@media(max-width: 992px){
    .secondscreen__body{
        flex-direction: column;
    }
}

.secondscreen__text{
    font-size: 21.3pt;
    margin-bottom: 8rem;
}
@media(max-width: 992px){
    .secondscreen__text{
        max-width: 100%;
        margin-bottom: 3rem;
    }
}

.secondscreen__info{
    max-width: 51%;
}
@media(max-width: 992px){
    .secondscreen__info{
        max-width: 100%;
    }
}

@media(max-width: 992px){
    .secondscreen__video{
        margin-top: 1.5rem;
    }
}

.secondscreen__industry{
    font-size: 24pt;
}
.secondscreen__industry span:nth-child(2){
    font-weight: bold;
    color: rgb(0,83,144);
}

/* Digits */
.digits{
    width: 65%;
    margin-top: 4rem;
}
@media(max-width: 992px){
    .digits{
        width: 100%;
    }
}
.digits__line{
    display: flex;
    align-items: center;
}
@media(max-width: 992px){
    .digits__line{
        flex-wrap: wrap;
    }
}
.digits__line img{
    max-width: 100%;
    height: auto;
}

/* Serv */
.serv{
    width: 35%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 4rem;
}
@media(max-width: 992px){
    .serv{
        width: 100%;
    }
}
.serv__heading{
    font-size: 16pt;
    font-weight: bold;
}
.serv__number{
    font-size: 32pt;
    color: rgb(0,83,144);
}
.serv__desc{
    font-size: 20pt;
}
.serv__text{
    width: 100%;
    font-size: 9pt;
    font-weight: 300;
    color: rgb(163,162,160);
}

/* Digit */
.digit{
    color: rgb(0,83,144);
    margin-right: 3rem;
}
.digit *{
    line-height: 1;
}
.digit__digit{
    font-weight: bold;
    font-size: 32pt;
}
.digit__lable{
    font-weight: 16pt;
    font-weight: 500;
    text-align: right;
}

/* iso */
.iso{
    margin-top: 4rem;
}
.iso > *{
    margin-right: 1rem;
}

/* fstek */
.fstek{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.fstek__lable{
    font-weight: bold;
    font-size: 12pt;
    color: rgb(9, 177, 186);
}

/* Footer */
.secondscreen__footer{
    display: flex;

    margin-top: 1rem;
}
@media(max-width: 992px){
    .secondscreen__footer{
        flex-wrap: wrap;
        margin-top: 2rem;
    }
}