.infographic{
    /* display: none; */
}
@media(max-width: 992px){
    .infographic{
        display: none;
    }
}
.infographic img{
    width: 100%;
    margin-top: 10vw;
}
.infographic__body{
    position: relative;
}
.infographic__body svg{
    position: absolute;
    right: 1rem;
    top: 1rem;

    width: 15vw;
    height: 15vw;
}
