.map{
    background-image: url(/assets/images/map_screen/map.png);
    background-repeat: no-repeat;
    background-size: 70%;
    background-position: right;
}
@media(max-width: 1460px){
    .map{
        background-size: 60%;
    }
}
@media(max-width: 1300px){
    .map{
        background-size: 52%;
    }
}
@media(max-width: 992px){
    .map{
        background-image: none;
    }
}
.map__t1{
    font-size: 18pt;
    margin-bottom: 2rem;
}
.map__t2{
    font-size: 32pt;
    color: rgb(38, 38, 38);
    margin-bottom: 1rem;
}
.map__t2 span{
    font-size: 58pt;
}
.map__t3{
    font-size: 18pt;
    margin-bottom: 1rem;
    padding-right: 51%;
}
@media(max-width: 1300px){
    .map__t3{
        padding-right: 51%;
    }
}
@media(max-width: 992px){
    .map__t3{
        padding-right: 0;
    }
}
.map__t4{
    font-size: 18.7pt;
    font-weight: bold;
}

/* Digits */
.map__digits{
    display: flex;
    justify-content: space-between;
    margin-top: 10rem;
}
@media(max-width: 1399px){
    .map__digits{
        margin-top: 4rem;
    }
}
.m-digit__title{
    font-size: 14pt;
}
.m-digit__number{
    font-size: 50pt;
    color: rgb(11, 94, 216);
    font-weight: bold;
    margin-left: 10px;
}
.m-digit__sub{
    font-size: 14pt;
}
@media(max-width: 992px){
    .m-digit{
        width: 100%;
        margin-bottom: 2rem;
        text-align: center;
    }
   .map__digits{
       flex-wrap: wrap;
       margin-top: 4rem;
   } 
}