.fourthscreen{
    min-height: 820px;
}
.fourthscreen__body{
    display: flex;
    align-items: center;
}
@media(max-width: 992px){
    .fourthscreen__body{
        flex-wrap: wrap;
    }
}
.fourthscreen__image{
    /* width: 55%; */
}
@media(max-width: 1399px){
    .fourthscreen__info{
        width: 55%;
        padding-left: 1.5rem!important;
    }
    .fourthscreen__image{
        width: 45%;
    }
}
@media(max-width: 992px){
    .fourthscreen__image{
        width: 100%;
    }
}
.fourthscreen__info{
    padding-left: 4rem;
    /* width: 45%; */
    flex-grow: 1;
}
@media(max-width: 992px){
    .fourthscreen__info{
        padding-left: 0;
        padding: 1rem 0;
    }
}
.fourthscreen__footer{
    background: rgb(191,191,191);
    margin-left: -4rem;
    margin-right: -4rem;
    position: absolute;
    width: 100%;
    bottom: 0px;
    padding: 4rem 8rem;

    display: flex;
    justify-content: space-between;
}
@media(max-width: 992px){
    .fourthscreen__footer{
        flex-wrap: wrap;
        padding: 2rem 0.5rem;

        margin-left: 0;
        margin-right: 0;
        position: relative;
    }
    .fourthscreen__footer::before, .fourthscreen__footer::after{
        content: '';
        display:block;
        position: absolute;
        top: 0;
        bottom: 0;
        background: rgb(191,191,191);
        width: 0.5rem;
    }
    .fourthscreen__footer::before{
        left: -0.5rem;
    }
    .fourthscreen__footer::after{
        right: -0.5rem;
    }
}

/* Card */
.card{
    display: flex;
    align-items: center;
    margin-bottom: 3rem;
}
.card__icon{
    width: 18%;
}
@media(max-width: 1200px){
    .card__icon{
        width: 23%;
    }
}
@media(max-width: 992px){
    .card__icon{
        width: initial;
    }
}
.card__data{
    width: 75%;
}
@media(max-width: 992px){
    .card__data{
        flex-grow: 1;
        width: initial;
        padding-left: 15px;
    }
}
.card__title{
    font-size: 18.7pt;
    color: rgb(0, 83, 144);
    font-weight: bold;
    margin-bottom: 1rem;
}
.card__text{
    font-size: 18.7pt;
}
@media(max-width: 1300px){
    .card__text{
        font-size: 14pt;
    }
}

/* Footer Block */
.fourthscreen__btmBlock{
    display: flex;
    
}
@media(max-width: 992px){
    .fourthscreen__btmBlock{
        flex-direction: column;
        margin-bottom: 2rem;
    }
}
.fourthscreen__btmBlock span{
    font-size: 18pt;
    color: rgb(0, 83, 144); 
    font-weight: bold;
    margin-right: 15px;
}
.fourthscreen__btmBlock p{
    font-size: 16pt;
    margin: 0;
}