.subtitle{
    font-size: 21.3pt;
    color: rgb(118, 113, 113);
    margin-top: 1.5rem;
}
.demand{
    font-size: 11pt;
    margin-top: 4rem;
}
.cities{
    margin-top: 4rem;
}

.city{
    display: flex;
    border-bottom: 3px dashed #e5e5e5;
}
.city:last-child{
    border-bottom: none;
}
.city__name{
    font-size: 18pt;
    color: white;
    /* background: rgb(0,83,144); */
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(255,255,255,1) 0%, rgba(0,83,144,1) 50%);
    padding: 2rem;
    min-width: 170px;
    position: relative;

    text-align: center;
    margin-left: -4rem;
    min-width: 200px;
}
@media(max-width:992px){
    .city__name{
        min-width: 170px;
        margin-left: 0;
    }
}
