/* Table */
.table{
    display: flex;
    margin-top: 1rem;
}
.table .table__header, .table .table__cell{
    width: 12.5%;
}
.table .table__header{
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.table .table__header .table__head{
    padding: 5px 0;
}
.table .table__header .head__value{
    flex-grow: 1;
    text-align: center;
    display: flex;
    align-items: center;
}
.table__header .table__head{
    font-size: 9pt;
    font-weight: bold;
}
.table__header .head__value{
    font-size: 9pt;
}

.table__cell .table__head{
    background: rgb(0,83,144);
    color: white;
    font-weight: bold;
    font-size: 14pt;
    line-height: 1.5;
    text-align: center;
    border-right: 1px solid white;
    padding: 8px 0px;
}
.table__cell .head__value{
    font-size: 10pt;
    line-height: 1.1;
    text-align: center;
    margin-top: 1rem;
    padding: 0 10px;
}
@media(max-width: 992px){
    .table{
        flex-wrap: wrap;
    }
    .table .table__header, .table .table__cell{
        width: 100%;
    }
    .table__cell .head__value{
        margin-bottom: 0.5rem;
    }
}