.thirteenthscreen{
    background-image: url(/assets/images/thirteenth/overlay.png), url(/assets/images/thirteenth/ball.jpg);
    background-size: contain, cover;
    background-repeat: no-repeat;
}
@media(max-width: 1200px){
    .thirteenthscreen{
        padding-top: 1rem;
    }
}
@media(max-width: 992px){
    .thirteenthscreen{
        display: none;
    }
}
.thirteenthscreen__title{
    font-size: 37.3pt;
    color: white;
}
.thirteenthscreen__title span{
    font-size: 64pt;
}