.section{
    padding: 4rem;
    min-height: 100vh;
}
@media(max-width: 992px){
    .section{
        padding: 0.5rem;
    }
    section{}
}
.section__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
@media(max-width: 992px){
    .section__header{
        flex-wrap: wrap;
    }
}
.section__title{
    font-size: 29.3pt;
    color: rgb(0, 83, 144);
    display: flex;
    align-items: center;
    max-width: 85%;
}
@media(max-width: 992px){
    .section__title{
        flex-wrap: wrap;
        max-width: 100%;
    }
}
.section__title img{
    margin-right: 25px;
}
@media(max-width: 992px){
    .section__header{
        flex-wrap: wrap;
        justify-content: flex-end;
    }
}