.awards{
    padding: 0;
}
.awards__heading{
    background: rgb(0, 83, 144);
    color: white;
    padding: 4rem;

    display: flex;
    justify-content: space-between;
}
@media(max-width: 992px){
    .awards__heading{
        flex-wrap: wrap;
        padding: 0.5rem;
    }
}
.awards__title{
    font-size: 21pt;

    width: 40%;
}
@media(max-width: 992px){
    .awards__title{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}
.awards__title strong{
    font-size: 37pt;
}
.awards__wrapper{
    width: 60%;
}
@media(max-width: 992px){
    .awards__wrapper{
        width: 100%;
    }
}
.awards__line{
    display: flex;
    justify-content: space-between;
    margin-bottom: 4rem;
}
@media(max-width: 992px){
    .awards__line{
        flex-wrap: wrap;
        margin-bottom: 2rem;
    }
}
.quote{
    margin-left: 7rem;
    font-size: 12pt;
}
@media(max-width: 992px){
    .quote{
        margin-left: 0;
        
    }
}

/* award */
.award{
    width: 30%;
}
@media(max-width: 992px){
    .award{
        width: 100%;
        text-align: center;
        margin-bottom: 2rem;
    }
}
.award p {
    font-size: 13.3pt;
}
.awards__prizes{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 4rem;
    margin-top: 1rem;
}
@media(max-width: 1399px){
    .award svg{
        max-width: 95%!important;
    }
}
@media(max-width: 992px){
    .awards__prizes{
        justify-content: center;
    }
}
.prize{
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    margin: 0.5rem;
}
.prize img{
    max-width: 176px;
}
.prize lable{
    margin-top: 0.5rem;
}