.lastscreen{
    min-height: 100vh;
    background-image: url(/assets/images/lastscreen.jpg);
    background-size: cover;
    background-position: center;

    position: relative;
}
.lastscreen__title{
    color: white;
    font-size: 27pt;
    font-weight: bold;
    max-width: 40%;
}
@media(max-width: 992px){
    .lastscreen__title{
        max-width: 100%;
        font-size: 17pt;
        margin-top: 3rem;
    }
}
.socials{
    color: white;
    font-size: 11pt;
    position: absolute;
    bottom: 2rem;
    right: 2rem;
}
.socials img{
    width: 20px;
    margin-right: 10px;
}
ul{
    padding-left: 0;
}
.socials ul li{
    list-style: none;
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}