.fifthscreen{

}
.fifthscreen__image{
    margin-left: -4rem;
    margin-right: -4rem;
    margin-top: 4rem;
}
@media(max-width: 992px){
    .fifthscreen__image{
        margin-left: 0;
        margin-right: 0;
        margin-top: 1rem;
    }
}
.fifthscreen__image img{
    width: 100%;
}
.fifthscreen__footer{
    display: flex;
    margin-top: 5rem;
}
@media(max-width: 992px){
    .fifthscreen__footer{
        flex-wrap: wrap;
    }
}

/* Circle stack */
.circle-stack{
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(58,58,58);
    margin-top: 4rem;
}
@media(max-width: 992px){
    .circle-stack{
        flex-direction: column;
    }
}
.circle{
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background: white;
    z-index: 2;
}   
.circle:before{
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    bottom: 0;
    display: block;
    width: 55%;
    height: 100%;
    z-index: 1;
    border-radius: 50%;
    border-bottom-left-radius: 800px;
    border-top-left-radius: 800px;
    background: rgb(0,83,144);
}
.circle__wrapper{
    position: relative;
    z-index: 5;
    background: white;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 2%;
}
.circle__lable{
    font-size: 12pt;
    text-align: center;
    max-width: 90%;
}

.circle__number{
    font-size: 22pt;
    font-weight: bold;
    text-align: center;
}

/* Provided */
.provided{
    display: flex;
    flex-wrap: wrap;
    width: 60%;
}
@media(max-width: 992px){
    .provided{
        width: 100%;
    }
}
.provided__info{
    display: flex;
    align-items: center;
    padding-right: 20%;
}
.provided__info svg{
    margin-right: 15px;
    min-width: 59px;
}
.provided__title{
    font-size: 16pt;
    font-weight: bold;
    width: 100%;
    margin-bottom: 1rem;
}
.provided__text{
    font-weight: 14pt;
}

/* f-icons */
.f-icons{
    width: 40%;
}
@media(max-width: 992px){
    .f-icons{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 2rem;
    }
    .f-icons svg{
        max-width: 100%;
    }
}
.f-icons__title{
    font-size: 16pt;
    font-weight: bold;
    width: 100%;
    margin-bottom: 1rem;
}