.thirdscreen{
    min-height: 100vh;
    background-image: url(/assets/images/thirdscreen/red_squre.jpg);
    background-size: cover;
    overflow: hidden;
    display: flex;
}
@media(max-width: 992px){
    .thirdscreen{
        background-position: center;
        padding: 0;
    }
}
.thirdscreen__body{
    width: 100%;
    position: relative;
}
.thirdscreen__content{
    background-image: url(/assets/images/thirdscreen/overlay.png);
    position: absolute;
    width: 100%;
    left: 35%;
    bottom: 0%;
    min-height: 708px;

    padding-top: 450px;
    padding-left: 310px;
}
@media(max-width: 992px){
    .thirdscreen__content{
        left: 0;
        padding-left: 0;
        padding-top: 545px;
    }
}
.thirdscreen__title{
    font-size: 42pt;
    color: white;
    margin: 0px;
}   
@media(max-width: 992px){
    .thirdscreen__title{
        font-size: 32pt;
        opacity: 0;
    }
}

.thirdscreen__title-mob{
    display: none;
    position: absolute;
    right: 2rem;
    bottom: 8rem;
    font-size: 20pt;
    color: white;
    line-height: 1.1;
    text-align: center;
}
@media(max-width: 992px){
    .thirdscreen__title-mob{
        display: flex;
        flex-direction: column;
    }
}

.thirdscreen__city{
    font-size: 24pt;
    color: rgb(255,192,0);
    margin-left: 220px;
}

.sixthscreen{
    background-image: url(/assets/images/sixthscreen/kazan.jpg);
}
.ninth{
    background-image: url(/assets/images/ninthscreen/vilnius.jpg);
}