.leaders__body{
    display: flex;
    margin-top: 4rem;
    justify-content: space-between;
}
@media(max-width: 992px){
    .leaders__body{
        flex-wrap: wrap;
    }
}
.col-3{
    width: 22%;

}
@media(max-width: 992px){
    .col-3{
        width: 100%;
        text-align: center;
        margin-bottom: 3rem;
    }
}
.col-6{
    width: 47%;
    padding: 0 1.5rem;
    padding-top: 2rem;
}
@media(max-width: 992px){
    .col-6{
        width: 100%;
        text-align: center;
        margin-bottom: 3rem;
    }
}
.leaders__body p,.leaders__body img{
    margin-bottom: 2rem;
}
.leaders__body p{
    font-size: 14pt;
}
.leaders__body span{
    font-weight: bold;
    color: rgb(11, 94, 216);
}
.frost-sullivan{
    padding-right: 5%;
}
@media(max-width: 992px){
    .frost-sullivan{
        padding-right: 0;
    }
}
.contact-center-world{
    margin: 0 auto;
    display: block;
}
.ecc-award{
    margin: 0 auto;
    display: block;
    margin-bottom: 0;
}
.merged-medals{
    margin-top: 2rem;
}
._12pt{
    font-size: 12pt!important;
}
.medals{
    display: flex;
    justify-content: space-between;
}
.medal{
    width: 47%;
}